// main: ../main.scss
//mobile first!!!!!!!!!!!!!!!!!
// Settings

$tablet-p-width: 768px;
$tablet-l-width: 960px;
$desktop-width:  1024px;
$widescreen-width: 1215px;
$full-hd-width: 1408px;

// Mixins
@mixin tablet-min{
	@media (min-width: #{$tablet-p-width}){
		@content;
	}
}
@mixin tablet-landscape-min{
	@media (min-width: #{$tablet-l-width}){
		@content;
	}
}
@mixin tablet-only {
  @media (min-width: #{$tablet-p-width}) and (max-width: #{$desktop-width}) {
    @content;
  }
}
@mixin desktop-min{
	@media (min-width: #{$desktop-width}){
		@content;
	}
}
@mixin widescreen-min{
	@media (min-width: #{$widescreen-width}){
		@content;
	}
}
@mixin full-hd-min{
	@media (min-width: #{$full-hd-width}){
		@content;
	}
}
@mixin retina{
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi){
    @content;
  }
}
